:root {
  /* Colors */
  --clr-primary-100: hsl(257deg, 40%, 49%);
  --clr-primary-200: hsl(300deg, 69%, 71%);
  /* Font Families */
  --ff-body: "Open Sans", sans-serif;
  --ff-headings: "Poppins", sans-serif;
  /* Font Sizes */
  --fs-body: 1rem;
  --fs-h1: clamp(1.5rem, 1.0535714286rem + 0.0178571429 * 100vw, 2.125rem);
  /* Font Weights */
  --fw-normal: 400;
  --fw-semi-bold: 600;
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
/* Typography */
body {
  font-family: var(--ff-body);
  font-size: var(--fs-body);
}

h1 {
  font-family: var(--ff-headings);
  font-size: var(--fs-h1);
  font-weight: var(--fw-semi-bold);
}

/* Page Styles */
body {
  background-color: var(--clr-primary-100);
  background-image: url("../img/bg-mobile.svg");
  background-size: cover;
  color: hsl(0deg, 0%, 100%);
}
@media (min-width: 60em) {
  body {
    background-image: url("../img/bg-desktop.svg");
  }
}

a {
  text-decoration: none;
}

/* Layout */
.container {
  margin: 0 auto;
  padding: 2.5rem;
}
@media (min-width: 60em) {
  .container {
    padding: 4.0625rem 5rem 2.5rem 5rem;
  }
}

/* Buttons */
.btn {
  display: inline-block;
  background-color: #ffffff;
  color: var(--clr-primary-100);
  width: 12.5rem;
  text-align: center;
  border-radius: 2rem;
  padding: 0.625rem 0;
  box-shadow: 3px 3px 7px 2px rgba(53, 48, 48, 0.38);
}
@media (min-width: 60em) {
  .btn {
    padding: 1rem 0;
  }
}
.btn:hover {
  background-color: var(--clr-primary-200);
  color: #ffffff;
}

.header {
  margin-bottom: 4.375rem;
}
@media (min-width: 60em) {
  .header {
    margin-bottom: 6.25rem;
  }
}

.logo {
  max-width: 7.875rem;
}
@media (min-width: 60em) {
  .logo {
    max-width: 12.5rem;
  }
}

.hero {
  display: grid;
  gap: 4.375rem;
  text-align: center;
  margin-bottom: 4.0625rem;
}
.hero h1, .hero p {
  margin-bottom: 1.875rem;
}
@media (min-width: 60em) {
  .hero {
    grid-template-columns: fit-content(43.4375rem) 1fr;
    gap: 3.4375rem;
    margin-bottom: 1.875rem;
  }
  .hero__text {
    text-align: start;
    padding-top: 3.4375rem;
  }
  .hero__text h1 {
    max-width: 20ch;
    margin-bottom: 2.5rem;
  }
  .hero__text p {
    line-height: 1.8;
    margin-bottom: 1.25rem;
  }
}

.footer {
  text-align: center;
}
.footer a:not(:last-child) {
  padding-right: 1rem;
}
.footer [class*=fa-] {
  color: hsl(0deg, 0%, 100%);
  height: 1.875rem;
}
.footer [class*=fa-]:hover {
  color: var(--clr-primary-200);
}
@media (min-width: 60em) {
  .footer {
    text-align: end;
  }
  .footer [class*=fa-] {
    height: 2.5rem;
  }
}